<template>
  <div class="grid grid-cols-3 gap-4">
    <div class="col-span-1">
      <Input v-model="data.title" :view="validation.title.$invalid ? 'danger' : 'default'" type="text" label="Title" block />
      <small class="text-red-base mt-2 block" v-for="(error, errorIndex) of validation.title.$errors" :key="errorIndex">{{ error.$message }}. </small>
    </div>
    <div class="col-span-2 col-start-1">
      <Input v-model="data.description" :view="validation.description.$invalid ? 'danger' : 'default'" type="textarea" label="Description" block />
      <small class="text-red-base mt-2 block" v-for="(error, errorIndex) of validation.description.$errors" :key="errorIndex">{{ error.$message }}. </small>
    </div>
    <div class="col-span-1 col-start-1">
      <span class="font-bold block mb-1">Keyword</span>
      <div class="border border-grey-01 p-4 rounded">
        <Button v-for="(value, keywordIndex) of data.keyword" :key="keywordIndex" class="mr-2 my-2" color="grey" view="smooth" dense>{{ value }} <i @click="removeKeyword(keywordIndex)" class="material-icons text-lg ml-1">close</i> </Button>
        <div class="flex flex-row items-center mt-4">
          <Input v-model="keyword" @keyup.enter="addKeyword" block />
          <Button class="ml-4" color="teal" view="smooth" @click="addKeyword">Add</Button>
        </div>
      </div>
    </div>
    <div class="col-start-1">
        <Tooltip effect="light" placement="bottom-start">
          <template #content>
          Do not enter full URL, only enter the subdirectory. For example: instead of <strong>[sellshark.com/how]</strong> enter <strong>[/how]</strong>
          </template>
          <Input v-model="data.url" :view="validation.url.$invalid ? 'danger' : 'default'" type="text" label="URL" block />
        </Tooltip>
        <small class="text-red-base mt-2 block" v-for="(error, errorIndex) of validation.url.$errors" :key="errorIndex">{{ error.$message }}. </small>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive, ref } from 'vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'

export default defineComponent({
  name: 'Company',
  components: {
    Input,
    Button
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        description: '',
        keyword: [],
        title: '',
        url: ''
      })
    }
  },
  setup(props) {
    const data = reactive(props.data)
    const rules = {
      description: { required },
      title: { required },
      url: { required }
    }
    const keyword = ref('')
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const removeKeyword = (keywordIndex) => {
      data.keyword.splice(keywordIndex, 1)
    }
    const addKeyword = () => {
      if (!keyword.value) {
        return
      }
      const keywords = keyword.value.split(',')
      data.keyword = [...data.keyword, ...keywords]
      keyword.value = ''
    }

    return {
      data,
      validation,
      keyword,
      validate,
      removeKeyword,
      addKeyword
    }
  },
})
</script>